.panel-buttons {
    min-width: 96px;
    .t-button { width: 100%; }

    .panel-button {
        width: 80%;
        padding: 3px 0;
    }
}

@media screen and (min-width: 768px) {
    .panel-buttons {
        position: relative;
        z-index: 5;
        height: 100%;

        .panel-button {
            width: 100%;
        }
    }
}